import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';

import Map from '../../components/map';

import PubSub, { topics } from '../../lib/pubsub';

const initialState = {};

@observer
class WearStatus extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	setWindpark = (e) => {
		const { windmillStore } = this.props.stores;
		const windpark = e.target.dataset.windpark;
		windmillStore.setCurrentWindparkName(windpark);
		// this.loadAll();
	}

	render() {
		const { appState, windmillStore } = this.props.stores;
		const { darkmode } = appState;
		const { windparks, currentWindparkName, currentWindpark } = windmillStore;

		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-4'>
						<h1><i class='fas fa-battery-three-quarters' /> Wear status</h1>
					</div>
					<div class='col-8 pt-2'>
						<ul class='nav nav-pills float-right'>
						{windparks && windparks.map(wp => (
							<li class='nav-item'>
								<button
									class={`btn btn-link nav-link ${wp.name === currentWindparkName ? 'active' : ''}`}
									onClick={this.setWindpark}
									data-windpark={wp.name}
								>{wp.name}</button>
							</li>
						))}
						</ul>
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4' style='height: 85vh;'>
						<Map stores={this.props.stores}
							darkmode={darkmode}
							center={[currentWindpark.position.lat, currentWindpark.position.lon]}
							zoom={currentWindpark.zoom}
							waypoints={currentWindpark.windmills}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default WearStatus;
